import React, { useState, useContext,useEffect } from "react";
import { Link, useHistory } from "react-router-dom";
import "../css/login.css";
import { ToastContainer, toast } from "react-toastify";
import UserContext from "../context/users/UserContext";

function Login(props) {
  const history = useHistory();

  useEffect(()=>{
    if (localStorage.getItem("auth-token")) {
      return history.push("/");
  }
// eslint-disable-next-line
  },[])
  const context = useContext(UserContext);
  const { setLoggedIn, updateCurrentUser } = context;
  const [details, uDetails] = useState({ email: "", password: "" });

  const updateDetails = (e) => {
    uDetails((prev) => {
      return { ...prev, [e.target.name]: e.target.value };
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const detailsToSubmit = {
      email: details.email,
      password: details.password,
    };
    const loginUser = async () => {
      try {
        // const host = "http://localhost:8000";
        const response = await fetch(`${process.env.REACT_APP_API_URL}auth/login`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(detailsToSubmit),
        });
        const data = await response.json();

        if (!data.success) {
          toast.error("Invalid Credentials");
          uDetails(() => {
            return { email: "", password: "" };
          });
          return;
        }

        localStorage.setItem("auth-token", data.jwt);
        setLoggedIn(()=>true);
        updateCurrentUser();
        toast.success("Login SuccessFul");
        toast("Welcome");
        setTimeout(() => {
          history.push("/");
        }, 2000);
      } catch (error) {
        toast.error("Some Error Occured ! TRY AGAIN");
      }
    };
    loginUser();
  };

  return (
    <div>
      <ToastContainer
        position="top-right"
        autoClose={5000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
      <Link className="bth" to="/">
        &#60; Back To Home
      </Link>
      <div className="logincontainer">
        <form className="loginbox" onSubmit={handleSubmit}>
          <div className="formControl">
            <h1>Login</h1>
          </div>
          <div className="formControl">
            <label htmlFor="email">EMAIL</label>
            <br />
            <br />
            <input
              type="email"
              name="email"
              onChange={updateDetails}
              value={details.email}
              required
            />
          </div>
          <div className="formControl">
            <label htmlFor="password">PASSWORD</label>
            <br />
            <br />
            <input
              type="password"
              name="password"
              onChange={updateDetails}
              minLength="8"
              value={details.password}
              required
            />
          </div>
          <div className="formControl">
            <button type="submit" className="submitok">
              Login
            </button>
          </div>
          <div className="formControl" id="donthavelogin">
            <div>
              Don't have an account? <Link to="signup">Create New</Link>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default Login;
